import React, {useEffect, useRef, useState} from 'react';
import { useToDoStore } from '../../../data/stores/useToDoStore.ts';
import { InputPlus } from '../InputPlus//index.tsx';
import { InputTask } from '../InputTask//index.tsx';
import { Droppable } from 'react-beautiful-dnd';

import styles from './index.module.scss';

interface ListProps {
	id: number;	
	title: string;	
	onEdited: (id: string, title: string) => void; 
	onRemoved: (id: string) => void; 
}

export const List: React.FC<ListProps> = ({
	id,
	title,
}) => {
	const [
		tasks,
		listId,
		createTask,
		updateTask,
		removeTask,
	] = useToDoStore(state => [
		state.tasks,
		state.listId,
		state.createTask,
		state.updateTask,
		state.removeTask,
	]);

	return (
		<div className={styles.list} id={id}>
			<h1 className={styles.list__title}>{title}</h1>
			<div className={styles.list__form}>
				<InputPlus
					id={id}
					onAdd={(title) => {
						if (title) {
							createTask(id, title);
						}
					}}
				/>
			</div>
			<div className={styles.list__elements}>
				{!tasks.length && (
					<p className={styles.list__text}>There in no one task</p>
				)}				
				{tasks.filter(task => task.listId == id).map((task) => {
					return <>
						<InputTask
							key={task.id}
							listId={id}
							id={task.id}
							title={task.title}
							onDone={removeTask}
							onEdited={updateTask}
							onRemoved={removeTask}
						/>
					</>
				})}
			</div>
		</div>
	)
}