import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { useToDoStore } from '../../data/stores/useToDoStore.ts';
import { List } from '../components/List/index.tsx';
import { DragDropContext } from 'react-beautiful-dnd';

import styles from './index.module.scss';
import 'swiper/css';
import "swiper/css/pagination";
import "swiper/css/navigation";

import { Pagination, Navigation } from "swiper";

export const App: React.FC = () => {
	const [
		tasks,
		createTask,
		updateTask,
		removeTask,
	] = useToDoStore(state => [
		state.tasks,
		state.createTask,
		state.updateTask,
		state.removeTask,
	]);

	function onDragEnd(result) {
		console.log("onDragEnd");
	}


	return (
		<DragDropContext onDragEnd={onDragEnd}>
			<Swiper
				spaceBetween={0}
				slidesPerView={"auto"}
				loop={false}
				pagination={{
					clickable: true,
				}}
				navigation={true}
				modules={[Pagination, Navigation]}
				onSlideChange={() => console.log('slide change')}
				onSwiper={(swiper) => console.log(swiper)}
				centeredSlides={true}
				breakpoints={{
					"200": {
						slidesPerView: 1,
						spaceBetween: 0,
						centeredSlides: true
					},
					"800": {
						slidesPerView: 2,
						spaceBetween: 0,
					},
					"1100": {
						slidesPerView: 3,
						spaceBetween: 0,
					},
					"1500": {
						slidesPerView: 4,
						spaceBetween: 0,
					},
				}}
				className="mySwiper"
			>
				<SwiperSlide><List
					id="3"
					key="3"
					title="  "
				></List></SwiperSlide>
				<SwiperSlide><List
					id="5"
					key="5"
					title="  "
				></List></SwiperSlide>
				<SwiperSlide><List
					id="1"
					key="1"
					title=" "
				></List>	</SwiperSlide>
				<SwiperSlide><List
					id="2"
					key="2"
					title="  "
				></List></SwiperSlide>
				<SwiperSlide><List
					id="4"
					key="4"
					title=""
				></List></SwiperSlide>				
				<SwiperSlide><List
					id="6"
					key="6"
					title="  "
				></List></SwiperSlide>
			</Swiper>
		</DragDropContext>
	);
}